*{
  margin: 0;
  padding: 0;
  
}

body{
 background-image:url('/REACT JS/portfolio-app/public/gatget.jpg');
 background-repeat: no-repeat;
 background-size: cover;
 backdrop-filter: blur(4px);

 
}

nav{text-align: center;
  padding: 10px;
  width:10%;
  margin: auto;
  overflow: hidden;
 box-shadow:2px 2px 2px rgb(155, 154, 154);
 margin-top: 5px; 
 transition: 0.5s;
 background-color: rgba(255, 255, 255, 0.753);
 border-radius: 10px;
 

  
  
}


nav:hover{
  width:50%;
  transition: 1s;
}
.heading{text-align: start;
  
  margin-left: 20px;
  color: rgb(253, 252, 252);
}
nav a{padding: 10px 20px;
  margin-right: 50px;
  text-decoration: none;
  color:rgb(9, 10, 10); 

  font-family: cursive;
}
nav a:hover{
  color: rgb(37, 162, 211);
 
 
}
.linkedin,.x,.git{
  padding: 20px;
  margin: 30px;
}
.links{margin-top: 20px;
  width: 400px;
}

#icon{scale: 3.0;
color: rgb(10, 10, 10);
transition: 0.5s;
}
#icon:hover{scale: 5.0;
transition: 0.5s;}

img{
  width:70%;
  height: 500px;
  padding-top: 20px;
  margin-left: 80px;
  
  padding-left: 50px;
  
}
h1 {
  font-size: 72px;
  background: -webkit-linear-gradient(45deg,rgb(251, 251, 252),rgb(226, 224, 224));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Lucida Handwriting", cursive;;
  padding-top: 45px;
}
.btn{margin-left: 45%;
 
  color: white;
}
.btn:hover{
  background-color: white;
  color: black;
}
.do{width: 150px;
  height: 45px;
  padding: 10px;
  position: absolute;
  top: 39%;
  left: 85%;
  background-color: brown;
  animation: typing 1s steps(40, end) alternate;
  overflow: hidden;
}

@keyframes typing {
  from { width: 0px; }
  to { width: 150px; }
}

#design{border: 1px solid rgb(8, 8, 8);
  margin-top: 35%;
  margin-left: 15%;
  padding: 20px;
  border-radius: 10px;
}
.about{
  background-image: linear-gradient(rgba(128, 128, 128, 0.582),white);

  color: black;
}

.contact{
  color: white;
}
.form{
  margin-top: 50px;
  margin-left: 20px;
  width: 45%;

  background-color: rgba(0, 0, 0, 0.425);
  padding: 30px;
  border-radius: 20px;
}
.sent{text-align: center;
  background-color: rgba(255, 255, 255, 0.555);
  margin-top: 10%;
  padding: 50px;
  
}
